import React, { useEffect, useState } from "react"
import Base from "../Components/Base"
import StudentItem from "../Components/Item/StudentItem"
import Loader from "../Components/Loader/Loader"
import Card from "../Components/Card/Card"
import { getStudents, uploadStudents } from "../Helpers"


const Student = () => {

    const [data, setData] = useState(null)
    const [file, setFile] = useState(null)
    const [error, setError] = useState(false)
    
    useEffect(() => {
        
        getStudents(1, 10)
            .then((data) => {
                setData(data.dbRes)
            })
            .catch(err => setError(true))
        
    }, [])

    const fileChange = (e) => {
        const f = e.target.files[0]
        var reader = new FileReader()
        reader.readAsDataURL(f)
        reader.onload = () => {
            setFile(reader.result)
        }
    }
    const submitForm = async (e) => {
        e.preventDefault()
        if(!file) 
            return alert("Choose A CSV File!")
        try {
            let upload = await uploadStudents({ csvFileBase64: file.split(',')[1] })
            if(upload.success) {
                alert("Students Uploaded! Refresh To See!")
            }else {
                alert("Something went wrong!")
            }
        }catch(err) {
            alert("Error Occured!")
        }
        
    }
    const changePage = async (page) => {
        try {
            let student = await getStudents(page, 10)
            setData(data.dbRes)
        }catch(error) {
            setError(true)
        }
    }
    
    return(
        <Base>
            <Card style="h-[80vh] w-[1600px] flex-col flex -ml-2 p-2">
                { error ? <p>An Error Occurred!</p> : ''}
                <div className="flex">
                    Bulk Add Students Using CSV :
                    <form onSubmit={submitForm}>
                        <input onChange={fileChange} type="file" name="file" id="file" />
                        <button className="btn w-[200px] h-[30px] bg-[#000] text-white">
                            Upload
                        </button>
                    </form>
                </div>
                {data ? <StudentItem data={data} /> : <Loader />}
                { !data ? '' :
                    <div className="flex flex-row justify-end">
                        <button disabled={!data.hasPrevPage ? true : false} onClick={() => changePage(data.prevPage)} className="btn w-[80px] h-[20px] bg-[#E9286D] text-center rounded text-white text-sm m-2">
                            Previous
                        </button>
                        <button disabled={!data.hasNextPage ? true : false} onClick={() => changePage(data.nextPage)} className="btn w-[80px] h-[20px] bg-[#E9286D] text-center rounded text-white text-sm m-2">
                            Next
                        </button>
                    </div>
                }
            </Card>
        </Base>
    )
}

export default Student